body {
    background-color: #725d6b;
    min-height: 100vh;
}

.todo-list-name {
    padding-top: 15vh;
    padding-bottom: 5vh;
}

.todo-list-name input {
    font-size: 3rem;
    text-align: center;
    cursor: pointer;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.todo-list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.todo {
    display: flex;
    align-items: center;
    margin: 1rem 0;
    border: solid #c0bbbb8f;
    padding: 2px 10px;
    border-radius: 16px;
}

.todo-is-completed .checkbox {
    color: #433c3c;
    background: #f0e5e5;
}

.todo-is-completed input {
    text-decoration: line-through;
    text-decoration-color: #c6b9b9;
    text-decoration-thickness: 2px;
}

.checkbox {
    width: 25px;
    height: 22px;
    border-radius: 50%;
    margin: 0 10px;
    cursor: pointer;
    font-size: 13px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color .2s ease-in-out;
    border: 1px solid #fff;
}

.checkbox:hover {
    background: rgba(255, 255, 255, 0.25);
    border: 1px solid rgba(255, 255, 255, 0);
}

.checkbox-checked {
    color: #000;
    background: #fff;
}

ul {
    list-style: none;
    padding: 0;
    line-height: 2rem;
    min-width: 60%;
    max-width: 80%;
}

input {
    border: none;
    background: transparent;
    color: white;
    font-size: 1.4rem;
    outline: none;
    width: 100%;
}

.btn-action {
    border-color: transparent;
    background: #c6b9b9;
    border-radius: 45%;
    color: #2f2b2b;
    cursor: pointer;
    width: 30px;
    height: 25px;
}

.btn-action:hover {
    background: #ece2e2;
}

.todo-btns-action {
    display: flex;
    justify-content: center;
}

